.Header {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 2vw 5vw;
    background-color: #3a466d;
    text-align: center;
    border: none;
}

.Header-con {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
}

.Header-img-text {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16vw;
    height: 16vw;
    border-radius: 50%;
    background: linear-gradient(to right, #f67e2b, #fe014d);
    font-weight: bold;
    font-size: 6vw;
    padding: 0;
    margin: 0;
}


.Logout-con {
    margin-top: 2vw;
    height: 10vw;
    width: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Logout-icon {
    width: 100%;
    height: 100%;
    aspect-ratio: 1;
}

.notification-con {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: end;
}

.notification-btn {
    position: relative;
    margin-top: 2vw;
    height: 10vw;
    width: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: ring 1s ease-in-out;
    transform-origin: top center;
}

.notification-icon {
    width: 100%;
    height: 100%;
    aspect-ratio: 1;
}

.notification-number {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: linear-gradient(to right, #f67e2b, #fe014d);
    padding: 0;
    margin: 0;
    width: 5vw;
    height: 5vw;
    border-radius: 50%;
}

.notification-number .TheNumber {
    padding: 0;
    margin: 0;
    font-size: 4vw;
    font-weight: 700;
    margin-top: -0.5vw;
}


@keyframes ring {
    0% {
        transform: rotate(0deg);
    }

    10% {
        transform: rotate(10deg);
    }

    30% {
        transform: rotate(-10deg);
    }

    60% {
        transform: rotate(10deg);
    }

    90% {
        transform: rotate(-10deg);
    }

    100% {
        transform: rotate(0deg);
    }
}