.Questions {
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 5vw;
    padding-top: 18vw;
    background-color: #272b46;
    color: white;
    opacity: 1;
    transform: translateY(0);
    transition: opacity 0.5s ease, transform 0.5s ease;
}

.Questions h1 {
    text-align: center;
    font-size: 7vw;
    margin: 0;
    margin-bottom: 4vw;
}

.faq-section {
    width: 90vw;
    max-width: 800px;
    margin-top: 2vw;
}

.faq-item {
    margin-bottom: 6vw;
}

.faq-item h2 {
    font-size: 5vw; /* Scalable FAQ question size */
    color: #f68b23;
    margin-bottom: 2vw;
}

.faq-item p {
    font-size: 4vw; /* Scalable FAQ answer size */
    line-height: 1.5;
    color: #ddd;
}

.Questions .goBack-icon {
    position: absolute;
    left: 3vw;
    top: 3vw;
    width: 10vw; /* Scalable size for the back icon */
    height: 10vw;
}

.Questions .logo-icon {
    position: absolute;
    right: 3vw;
    top: 3vw;
    width: 10vw; /* Scalable size for the logo */
    height: 10vw;
}

.faq-login-link {
    margin-top: 8vw;
}

.back-to-login-button {
    background-color: #fc014f;
    color: white;
    border: none;
    padding: 4vw 6vw; /* Scalable padding */
    font-size: 4.5vw; /* Scalable font size for the button */
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.back-to-login-button:hover {
    background-color: #a50236;
}
