.transaction-list {
    background-color: #3a466d;
    color: white;
    border-radius: 0.5rem;
    overflow: hidden;
}

.transaction-list .transaction-title {
    background-color: #3a466d;
    padding: 1rem;
    border-bottom: 2px #5b628d solid;
}

.transaction-list .transaction-title .title {
    margin: 0;
    padding: 0;
    font-size: 6vw;
}


.transaction-list ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    padding: 0;
    gap: 0;
    background-color: #343d5e;
}

.transaction-list li {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 0.3rem;
    padding: 1rem;
    border-bottom: 2px #5b628d solid;
    position: relative;
}

.transaction-list li:last-child {
    border: none;
}

.transaction-list li .data {
    text-align: start;
    display: flex;
    justify-content: space-between;
}

.transaction-list li .data .description {
    font-size: 5vw;
    margin: 0;
    padding: 0;
    font-weight: 700;
    line-break: anywhere;
    max-width: 50%;
}

.transaction-list li .data .amount {
    text-align: end;
    font-size: 5vw;
    margin: 0;
    padding: 0;
    font-weight: 700;
    max-width: 50%;
}

.data .income {
    color: #2668fd;
}

.data .expense {
    color: #fc004f;
}

.transaction-list li .info {
    text-align: end;
    display: flex;
    justify-content: space-between;
}

.transaction-list li .info .text {
    font-size: 4vw;
    margin: 0;
    padding: 0;
    color: rgba(255, 255, 255, 0.5);
}