.profile {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.user-info {
    background-color: #3a466d;
    padding: 1rem;
    border-radius: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 0.5rem;
}
.profile-title {
    margin: 0;
    padding: 0;
    font-size: 5vw;
}
.profile-text {
    margin: 0;
    padding: 0;
    font-size: 5vw;
}

.profile-text-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.profile-split {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.profile-img-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.profile-img-text {
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: linear-gradient(to top, #f6af2b, #fe024c);
    border-radius: 50%;
    font-size: 8vw;
    aspect-ratio: 1;
    padding: 6vw;
    margin: 0;
}

.account-info {
    background-color: #3a466d;
    padding: 1rem;
    border-radius: 0.8rem;
}

.account-details {
    border-bottom: 2px #5b628d solid;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 0;
}

.account-details:last-child {
    border: none;
}
.account-info-title {
    text-align: center;
    font-size: 7vw;
    margin: 0;
    padding: 0;
    margin-bottom: 1rem;
}

.account-info-text {
    font-size: 4.5vw;
    margin: 0;
    padding: 0;
}