.balance-card {
    background-color: #3a466d;
    color: white;
    border-radius: 10px;
    text-align: center;
    overflow: hidden;
}

.balance-card .Title-Container {
    padding: 1rem;
    text-align: start;
}

.balance-card .Title-Container .balanceTitle {
    margin: 0;
    padding: 0;
}

.balance-card .balance-Container {
    background-color: #343d5e;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.balance-card .balance-Container .balance-item {
    background-color: #3a466d;
    height: auto;
    border-radius: 1rem;
}

.balance-item .balance-bar {
    display: flex;
    padding: 0.7rem;
    border-bottom: 2px #5b628d solid;
}
.balance-item .balance-bar:last-child {
    border: none;
}

.balance-item .balance-bar .balance-texts {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.balance-item .balance-bar .balance-texts .balance-title {
    text-align: start;
    margin: 0;
    padding: 0;
    font-size: 5vw;
    font-weight: 700;
}

.balance-item .balance-bar .balance-texts .balance-text {
    text-align: start;
    margin: 0;
    padding: 0;
    font-size: 4vw;
    color: rgba(255, 255, 255, 0.5);
    font-weight: 500;
}

.balance-item .balance-bar .balance-number {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    margin-left: 0.5rem;
}

.balance-item .balance-bar .balance-number .balance-numbers {
    text-align: end;
    margin: 0;
    padding: 0;
    font-size: 5vw;
    font-weight: 700;
    margin-top: -1vw;
}