/* Loader.css */
.loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #272b46;
    z-index: 9999;
    transition: opacity 0.5s ease;
}

.loader-logo {
    width: 100px;
    height: 100px;
    animation: spin 3s linear infinite;
    transition: transform 0.5s ease;
}

.loading {
    opacity: 1;
}

.loaded {
    opacity: 0;
    transform: scale(0);
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}