.setting-item {
    padding: 1rem;
    display: flex;
    align-items: center;
    border-bottom: 2px #5b628d solid;
}

.setting-item:last-child {
    border: none;
}

.setting-info {
    flex: 1;
}

.toggle-switch {
    position: relative;
    display: block;
    width: 18vw;
    height: 8vw;
    margin-left: 1rem;
}

.setting-Title {
    padding: 0;
    margin: 0;
    font-size: 5vw;
}

.setting-text {
    padding: 0;
    margin: 0;
    font-size: 4vw;
    color: rgba(255, 255, 255, 0.5);
}

.toggle-switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #282a48;
    transition: .4s;
    border-radius: 8vw;
}

.slider:before {
    position: absolute;
    content: "";
    height: 6vw;
    width: 6vw;
    left: 1vw;
    bottom: 1.05vw;
    background-color: #fb0363;
    transition: .4s;
    border-radius: 50%;
}

/* Checked state styles */
input:checked+.slider {
    background-color: #2761e3;
}

input:checked+.slider:before {
    transform: translateX(10vw);
    background-color: #ffffff;
}